@import url(https://fonts.googleapis.com/css?family=Play:700|Roboto+Mono:500&display=swap);
@import url(https://kit.fontawesome.com/000cbe3da1.js);
body {
  margin: 0;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
  line-height: 1.15;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%; }

body {
  margin: 0; }

article, aside, footer, header, nav, section {
  display: block; }

h1 {
  font-size: 2em;
  margin: .67em 0; }

figcaption, figure, main {
  display: block; }

figure {
  margin: 1em 40px; }

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible; }

pre {
  font-family: monospace,monospace;
  font-size: 1em; }

a {
  background-color: transparent;
  -webkit-text-decoration-skip: objects; }

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  -webkit-text-decoration: underline dotted;
          text-decoration: underline dotted; }

b, strong {
  font-weight: inherit; }

b, strong {
  font-weight: bolder; }

code, kbd, samp {
  font-family: monospace,monospace;
  font-size: 1em; }

dfn {
  font-style: italic; }

mark {
  background-color: #ff0;
  color: #000; }

small {
  font-size: 80%; }

sub, sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline; }

sub {
  bottom: -.25em; }

sup {
  top: -.5em; }

audio, video {
  display: inline-block; }

audio:not([controls]) {
  display: none;
  height: 0; }

img {
  border-style: none; }

svg:not(:root) {
  overflow: hidden; }

button, input, optgroup, select, textarea {
  font-family: sans-serif;
  font-size: 100%;
  line-height: 1.15;
  margin: 0; }

button, input {
  overflow: visible; }

button, select {
  text-transform: none; }

button, html [type=button], [type=reset], [type=submit] {
  -webkit-appearance: button; }

button::-moz-focus-inner, [type=button]::-moz-focus-inner, [type=reset]::-moz-focus-inner, [type=submit]::-moz-focus-inner {
  border-style: none;
  padding: 0; }

button:-moz-focusring, [type=button]:-moz-focusring, [type=reset]:-moz-focusring, [type=submit]:-moz-focusring {
  outline: 1px dotted ButtonText; }

fieldset {
  padding: .35em .75em .625em; }

legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal; }

progress {
  display: inline-block;
  vertical-align: baseline; }

textarea {
  overflow: auto; }

[type=checkbox], [type=radio] {
  box-sizing: border-box;
  padding: 0; }

[type=number]::-webkit-inner-spin-button, [type=number]::-webkit-outer-spin-button {
  height: auto; }

[type=search] {
  -webkit-appearance: textfield;
  outline-offset: -2px; }

[type=search]::-webkit-search-cancel-button, [type=search]::-webkit-search-decoration {
  -webkit-appearance: none; }

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit; }

details, menu {
  display: block; }

summary {
  display: list-item; }

canvas {
  display: inline-block; }

template {
  display: none; }

[hidden] {
  display: none; }

.clearfix:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: '';
  clear: both;
  height: 0; }

html {
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

.visuallyhidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0);
  overflow: hidden; }

html {
  background: #101010;
  color: whitesmoke;
  font-family: "Roboto Mono", monospace; }

ul {
  list-style-type: none;
  padding: 0;
  margin: 0; }

.App {
  /* text-align: center; */
  display: flex; }

.wrapper {
  width: 90%;
  margin: 0 auto; }

.title {
  border-bottom: 1px solid white;
  padding: 10% 10%; }
  .title h1 {
    width: 100%;
    font-size: 4rem;
    font-family: "Play", sans-serif;
    margin: 0;
    line-height: 80%; }

main {
  width: 100%;
  background: black; }

@-webkit-keyframes activeList {
  0% {
    -webkit-transform: translateX(0);
            transform: translateX(0); }
  100% {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%); } }

@keyframes activeList {
  0% {
    -webkit-transform: translateX(0);
            transform: translateX(0); }
  100% {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%); } }

.banner {
  height: 80px;
  width: 100%;
  background: black;
  border-bottom: 2px solid white;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  position: relative; }
  .banner .activeList {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-around; }
  .banner li {
    border: 1px solid white;
    padding: 10px; }
  .banner h4 {
    margin: 0; }
  .banner p {
    margin: 0; }
  .banner .fa {
    font-size: 1.6rem; }

nav {
  width: 35%;
  height: 100vh;
  border-right: 1px solid white; }

nav .wrapper {
  display: flex;
  flex-direction: column;
  align-items: center; }

.searchAutofill {
  width: 85%;
  margin: 0 auto;
  margin-top: 50px;
  display: flex;
  justify-content: center;
  position: relative; }

form {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative; }

.searchBar {
  width: 85%;
  position: relative;
  display: inline-block; }

input {
  width: 0;
  font-size: 1.1rem;
  font-family: "Roboto Mono", monospace;
  height: 50px;
  padding: 0;
  border: 0;
  -webkit-transition: 1s;
  transition: 1s;
  -webkit-transition-delay: 1s;
          transition-delay: 1s; }

form:hover input {
  width: 95%;
  padding: 0 0 0 5%;
  -webkit-transition: 0.8s;
  transition: 0.8s; }

.searchButton {
  width: 100%;
  height: 50px;
  background-color: #333;
  color: #fff;
  border: 0;
  position: absolute;
  top: 0;
  right: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  -webkit-transition: 1s;
  transition: 1s;
  -webkit-transition-delay: 1s;
          transition-delay: 1s; }
  .searchButton .fa {
    font-size: 1rem; }

form:hover button {
  width: 50px;
  -webkit-transition: 0.8s;
  transition: 0.8s; }

.dropdownSearch {
  position: absolute;
  z-index: 10;
  top: 0;
  background-color: #282c34;
  width: 85%;
  margin-top: 50px; }
  .dropdownSearch li {
    padding: 1% 0 0 5%; }

.chartNumbersInfo h2 {
  width: 90%;
  font-size: 3rem;
  margin: 0;
  padding: 3% 0 1% 0;
  border-bottom: 1px solid white;
  -webkit-transition: 0.5s;
  transition: 0.5s; }

.numbersGraph {
  margin-top: 5%;
  display: flex;
  justify-content: space-between; }

.companyNumbers {
  display: flex;
  justify-content: space-between;
  width: 35%;
  font-size: 1.4rem; }

.stockInformation {
  line-height: 35px;
  width: 100%; }
  .stockInformation li {
    width: 100%;
    padding: 2% 0 0% 0;
    display: flex;
    justify-content: space-between;
    border-bottom: 1px white dotted; }

